#search-box input,
#search-box button {
    float: left;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    outline: none;
    -webkit-appearance: none;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box; }

#search-box input {
    padding-left: 15px;
    width: 100%;
    height: 37px;
    font-size: 12pt;
    font-weight: 400;
    color: #111;
    border: 1px solid #DDD; }

#search-box button {
    padding: 8px 15px;
    background-color: #333;
    color: #FAFAFA;
    border: 1px solid transparent; }

#search-results {
    float: left;
    margin-top: 10px;
    width: 100%; }

#search-results > div {
    padding: 10px 5px 10px 5px;
    margin-bottom: 15px; }

#search-results a {
    display: block; }

#search-results span {
    font-size: 0.8em; }
